
.homepage{background:#f1f1f1;padding-bottom:1.6rem
}
.homepage .locate{position:fixed;bottom:0;text-align:center;height:1.36rem;line-height:1.35rem;width:100%;background:#fff
}
.homepage .locate a{width:90%;background:#ff6765;color:#fff;text-align:center;display:inline-block;line-height:.8rem;height:.8rem;border-radius:.1rem;font-size:.36rem
}
.homepage header{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;height:.7rem;background:#ed435b
}
.homepage header img{height:.3rem;margin-left:.2rem;margin-right:.1rem
}
.homepage header span{color:#fff;font-size:.28rem;height:.7rem;line-height:.7rem
}
.homepage .mall-banner img{width:100%
}
.homepage .mobile-flow-title{padding-top:.5rem;padding-bottom:.6rem;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center
}
.homepage .mobile-flow-title span{width:.6rem;height:.01rem;background:#343434
}
.homepage .mobile-flow-title p{font-size:.26rem;margin:0 .1rem
}
.homepage .mobile-flow{margin:.26rem 0;background:#fff
}
.homepage .mobile-flow .process{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;padding:0 .32rem;font-size:.22rem;color:#626262;padding-bottom:.4rem
}
.homepage .mobile-flow .process .item{text-align:center;-webkit-box-flex:1;-ms-flex:1;flex:1;position:relative
}
.homepage .mobile-flow .process .item span{display:block;margin-top:.18rem
}
.homepage .mobile-flow .process .item.icon i{position:absolute;right:0;top:.25rem
}
.homepage .reason{background:#fff
}
.homepage .reason .reason-item{padding-bottom:.6rem;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-align:center;-ms-flex-align:center;align-items:center;position:relative;padding-right:.2rem
}
.homepage .reason .reason-item .img{height:100%;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-ms-flex-line-pack:center;align-content:center;-webkit-box-flex:3;-ms-flex:3;flex:3
}
.homepage .reason .reason-item .content{-webkit-box-flex:13;-ms-flex:13;flex:13
}
.homepage .reason .reason-item .content .title{font-size:.26rem;line-height:1.25;margin-bottom:.1rem
}
.homepage .reason .reason-item .content .title b{margin-right:.1rem
}
.homepage .reason .reason-item .content .detail{font-size:.22rem;color:#626262;line-height:1.5
}
.homepage .reason .reason-item .content .detail span{margin-right:.1rem
}
.red{color:#ff5454
}


